              font-family: Lato;
              font-style: normal;
              font-weight: normal;
              font-size: 12px;
              line-height: 14px;
              color: #878787;
            }
            p {
              margin: 0px;
              font-family: Lato;
              font-style: normal;
              font-weight: 600;
              font-size: 12px;
              line-height: 14px;
              color: #333333;
              background-color: unset;
            }
          }
          button {
            padding: 15px;
            background-color: #1396E7;
            font-family: Lato;
            font-style: normal;
            font-weight: 500;
            font-size: 12px;
            line-height: 14px;
            color: #FFFFFF;
            border: 1px solid #1396E7;
            transition: all .25s;
            &:hover {
              opacity: 0.6;
            }
          }
          p {
            font-size: 12px;
            margin-top: 15px;
            margin-bottom: 0px;
            text-align: center;
            position: relative;
            line-height: 140%;
            background: hsla(203, 84%, 71%, 0.667);
            color: white;
            padding: 5px;
            border-radius: 8px;
            .times {
              position: absolute;
              top: 0px;
              right: 3px;
              cursor: pointer;
              font-size: 20px;
            }
