}

.d-flex {
  display: flex;
  &.align-items-center {
    align-items: center;
  }
  &.justify-content-between {
    justify-content: space-between;
  }
}

/* The container */
.radio-container {
  display: block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
  }
  .checkmark {
    position: absolute;
    top: -2px;
    left: 0;
    height: 16px;
    width: 16px;
    background-color: #FFF;
    border: 1px solid #1396e7;
    border-radius: 5px;
  }
  &:hover {
    input ~ .checkmark {
      background-color: #ccc;
    }
  }
  input:checked ~ .checkmark {
    background-color: #FFF;
    &::after {
      display: block;
    }
  }
  .checkmark::after {
    content: "";
    position: absolute;
    display: none;
    top: 2px;
    left: 2px;
    width: 12px;
    height: 12px;
    border-radius: 3px;
    background: #1396e7;
  }
}

.slider-bar {
  padding: 20px 0px;
  flex: 1;
  .horizontal-slider {
    flex: 1;
    margin-right: 24px;
    margin-top: 20px;
    height: 4px;
    border-radius: 13px;
    .example-thumb {
        width: 10px;
        height: 10px;
        top: -3px;
        border-radius: 50%;
        background-color: #1396E7;
        font-size: 0px;
