        cursor: pointer;
    }
    .example-track.example-track-0 {
      background-color: #1396E7;
      height: 4px;
      overflow: hidden;
    }
    .example-track.example-track-1 {
      background-color: #F1F1F1;
      height: 4px;
      overflow: hidden;
    }
  }
  .minmax {
      padding-left: 20px;
      padding-right: 65px;
  }
}

.btn-add-cart {
