              }
            }
            &:not(:nth-child(8n)):not(:nth-child(8n+7)) {
              .window-layout-img {
                width: calc(100% + 10px);
                left: -5px;
                right: -5px;
              }
            }
          }
        }
      }
      &.williams-1 {
        > * {
          &:nth-child(4n) {
            .window-layout-img {
              width: calc(100% + 5px);
              right: -5px;
            }
          }
          &:nth-child(4n + 3) {
            .window-layout-img {
              width: calc(100% + 5px);
              left: -5px;
            }
          }
          &:not(:nth-child(4n)):not(:nth-child(4n+3)) {
            .window-layout-img {
              width: calc(100% + 10px);
              left: -5px;
              right: -5px;
            }
          }
        }
      }
    }
  }
}
